import React from "react";

export type PageData = {
  id: string;
  brief: string;
  title: string;
  subtitle: string;
  url: string;
  slug: string;
  publishedAt: string;
  coverImage: {
      url: string;
  };
  content: {
      html: string;
  };
  seo: {
      title: string;
      description: string;
  };
}

export const SEO = ({pageData, ogImageUrl} : {pageData: PageData, ogImageUrl?: string}) => {
  const {id, title, brief, url, slug, publishedAt, coverImage, content, seo} = pageData

    return (
        <>
            <title>{title} | xprilion&apos;s blog</title>
            <meta itemProp="name" content={`${title} | xprilion's blog`}/>
            
            <meta name="description" content={brief}/>
            <meta property="og:description" content={brief}/>
            
            <meta name="twitter:title" content={`${title} | xprilion's blog`}/>
            <meta name="twitter:description" content={brief}/>
            
            <meta property="og:locale" content="en_US"/>
            <meta property="og:url" content={url}/>
            <meta property="og:type" content="article"/>
            
            {ogImageUrl && <meta property="image" content={ogImageUrl}/>}
            {ogImageUrl && <meta property="og:image" content={ogImageUrl}/>}
            {ogImageUrl && <meta property="twitter:image" content={ogImageUrl}/>}
        </>
    );
}